import { Header } from "components/Header"
import { InputBox, Btn } from "components/Common"
import styles from "styles/pages/loginPage.module.css"
import { Link, useNavigate } from "react-router-dom"
import { useState } from "react"
import { loginApi } from "api/twitterAPI"

export default function LoginPage() {
  //1. input 
  const [account, setAccount] = useState("")
  const [password, setPassword] = useState("")
  //2. input 
  const [accountError, setAccountError] = useState(false)
  const [passwordError, setPasswordError] = useState(false)
  const [wordNumberError, setWordNumberError] = useState(false)
  const [tokenError, setTokenError] = useState(false)
  const navigate = useNavigate()

  async function handleSubmit() {
    //1.,  input 
    if(account.length >= 50){
      setWordNumberError(true)
    }
    if (account.trim().length <= 0) {
      setAccountError(true)
    }
    if (password.trim().length <= 0) {
      setPasswordError(true)
    }
    if(
      account.trim().length > 0 &&
      password.trim().length > 0
    ){
      //2. 
      const data = await loginApi(account, password)
      if (data.status === "error") {
        console.log(" message: ", data.message)
        setTokenError(true)
        return
      }
      localStorage.setItem("authToken", data.token)
      localStorage.setItem("id", data.user.id)
      alert('')
      return navigate("/tweets")
    }
  }
  const handleKeyEnter = (e) => {
    if (e.key === "Enter") {
      handleSubmit()
    }
  }
  return (
    <div
      className="container"
      onKeyDown={(e) => {
        handleKeyEnter(e)
      }}
    >
      <div className={styles.fixedWidth}>
        <div className="row justify-content-center">
          <Header title=" Alphitter" />
        </div>
        <div className={styles.inputRow}>
          <InputBox
            className={
              accountError
                ? "error"
                : wordNumberError
                ? "error"
                : tokenError
                ? "error"
                : null
            }
            label=""
            type="text"
            placeHolder=""
            value={account}
            warningMessage={
              accountError
                ? ""
                : wordNumberError
                ? ""
                : tokenError
                ? ""
                : null
            }
            onChange={(e)=>{
              if(accountError){ 
                if(wordNumberError){setWordNumberError(null)}
                setAccountError(null)}              
                setAccount(e.target.value)
            }}
          />
        </div>
        <div className={styles.inputRow}>
          <InputBox
            className={passwordError ? "error" : tokenError ? "error" : null}
            label=""
            type="password"
            placeHolder=""
            value={password}
            warningMessage={
              passwordError
                ? ""
                : tokenError
                ? ""
                : null
            }
            onChange={(e) => {
              if (wordNumberError) {
                setWordNumberError(null)
              }
              if (passwordError) {
                setPasswordError(null)
              }
              setPassword(e.target.value)
            }}
          />
        </div>
        <div className={styles.btnRow}>
          <Btn className="btnRoundColor" text="" onClick={handleSubmit} />
        </div>
        <div className={styles.linkRow}>
          <Link to="/register"></Link>
          <span className={styles.dot}></span>
          <Link to="/admin"></Link>
        </div>
      </div>
    </div>
  )
}
