import { deleteTweetApi, getAdminTweets } from "api/twitterAPI"
import { AdminContent } from "components/AdminContent"
import AdminTweet from "components/AdminTweet"
import { useEffect, useState } from "react"
import { useNavigate } from "react-router"

// '' 
export default function AdminTweetListPage() {
  const [tweets, setTweets] = useState(null)
  const navigate = useNavigate()

  useEffect(() => {
    const getData = async () => {
      //
      const data = await getAdminTweets()
      if (data.status === "error") {
        alert("")
        console.error(" message: ", data.message)
        navigate("/login")
        return
      }
      setTweets(data)
    }
    getData()
  }, [])

  //  -  AdminTweet 
  const handleDeleteTweet = async (id) => {
    const message = await deleteTweetApi(id)
    setTweets(tweets.filter((tweet) => tweet.id !== id))
  }
  //
  let userTweets = tweets?.map((tweet) => {
    return (
      <AdminTweet
        key={tweet.id}
        id={tweet.id}
        name={tweet.User.name}
        account={tweet.User.account}
        avatar={tweet.User.avatar}
        description={tweet.description}
        createdAt={tweet.createdAt}
        handleDeleteTweet={handleDeleteTweet}
      />
    )
  })

  return (
    // AdminContent  '' ,  AdminTweet 
    <AdminContent title={""}>{userTweets}</AdminContent>
  )
}
