import styles from "styles/components/adminUserListPage.module.css"
import { AdminContent } from "components/AdminContent"
import { useEffect, useState } from "react"
import { getAdminUsersApi } from "api/twitterAPI"
import UserCard from "components/UserCard"
import { useNavigate } from "react-router"

export default function AdminUserListPage() {
  const [users, setUsers] = useState(null)
  const navigate = useNavigate()

  useEffect(() => {
    const getData = async () => {
      //
      const data = await getAdminUsersApi()
      if (data === "") {
        alert("")
        navigate("/login")
      }
      if (data.status === "error") {
        navigate("/login")
      }
      console.log(data)
      setUsers(data)
    }
    getData()
  }, [])

  //
  const usersList = users?.data.map((user) => {
    return (
      <UserCard
        key={user.id}
        cover={user.coverPage}
        avatar={user.avatar}
        name={user.name}
        account={user.account}
        tweets={user.tweetCounts}
        likes={user.likeCounts}
        followings={user.followingCounts}
        followers={user.followerCounts}
      />
    )
  })

  return (
    // AdminContent  '' ,  UserCard 
    <AdminContent title={""}>
      <div className={`container pt-3 ${styles.wrap}`}>
        <div className="row row-cols-auto g-3">{usersList}</div>
      </div>
    </AdminContent>
  )
}
