export const patchSetting = async (id, payload) => {
  try {
    const res = await axiosInstance.put(
      `${baseUrl}/users/${id}/setting`,
      payload
    )
    return res.data
  } catch (error) {
    //error  data  error.response.data
    console.log(error.response.data.message)
    return error.response.data
  }
}
