export const addTweet = async (text) => {
  try {
    const res = await axiosInstance.post(`${baseUrl}/tweets`, {
      description: text,
    })
    console.log(": ", res)
    const data = res.data
    if (data.status === "error") {
      return "error"
    }
    return data
  } catch (error) {
    console.error("Post ", error)
  }
}
