export const putUserProfile = async (payload) => {
  const formData = payload.formData
  const id = formData.get("id")

  try {
    const res = await axiosInstance.put(`${baseUrl}/users/${id}`, formData)
    return res.data
  } catch (error) {
    console.error("[Put user profile failed]: ", error)
  }
}
